<template>
 <div class="line" :class="[dirClass]" >

 </div>
</template>

<script type="text/ecmascript-6">
export default {
  props: {
    dirClass: {
      type: String
    }
  },
  data() {
    return {
    }
  },
  components: {
  }
}
</script>

<style scoped lang="less">
.line{
  position: fixed; 
  z-index: 4;
  background: linear-gradient(to left, #ddd 50%, #ddd 50%);
  background-repeat: no-repeat;
  background-size: 200% 200%;
  background-position: right bottom; 
  transition: background-position 0.6s 1.2s ease-out;
}
.line.active{ 
        background-repeat: no-repeat;
    background-size: 200% 200%;
    transition: background-position 0.2s 0.4s ease-out;
}
.bottom{ 
    background-position: left bottom;
    background: linear-gradient(to left, #ddd 50%, #1b396e 50%); 
}
.line-top{
  width:100%;
  height: 16px;
  top: 0;
}
.line-bottom{
  width:100%;
  height: 16px;
  bottom: 0;
}
.line-left{
  height:100%;
  width: 16px;
  left: 0;
  top:0;
}
.line-right{
  height:100%;
  width: 16px;
  right: 0;
  top:0;
}
 
 
</style>



