<template>
 <header class="header-wrapper">
   <div class="logo">
    <img src="./logo.png" alt="logo" width="60" height="80">
   </div>
   <div class="logo-text">
     Light Ming<br>
     <span>The Terminal Way To U</span>
   </div>
   <header-menu></header-menu>
 </header>
</template>

<script type="text/ecmascript-6">
import HeaderMenu from './HeaderMenu'
export default {
  data() {
    return {
    }
  },
  components: {
    HeaderMenu
  }
}
</script>

<style scoped lang="less">
.header-wrapper{
  position: absolute;
  top: 65px;
  left: 50%;
  transform: translateX(-50%);
  width: 80%; 
  height: 160px;
  .logo{
    float: left;
    margin-right: 20px;
    width: 60px;
    height: 80px;;
  }
  .logo-text{
    width: 250px;
    height: 65px;
    margin-top: 15px;
    float: left;
    font-weight: normal;
    font-size: 18px;
    color: #000;
    text-transform: uppercase;
    letter-spacing: 2px;
    transition: color 0.3s 2.4s ease-out;
    cursor: pointer;
    span{
      font-size: 13px;
      line-height: 30px;
      color: #000;
      opacity: 0.4;
      font-weight: normal;
      letter-spacing: 3px;
      transition: color 0.3s 2.4s ease-out;
    }
  }
}
 
</style>



