<template>
 <div class="main-wrapper" ref="mainw"> 
    <div class="grid left"> 
        <div class="item-wrapper" v-for="(item,index) in getItems('left')"   :key="index" @click="itemClick(item)">
          <div class="item-img" :style="{backgroundColor:item.bgcolor}">
            <img :src="require('../../common/images/'+item.name)" width="700" height="557"/>
          </div>
          <h2>{{item.title}}</h2>
          <div class="info">
            <div class="line" :style="getStyleObj(item.bgcolor)"></div>
            {{item.subtitle}}<br>
            {{item.subtitle_}}<br>
            <span>{{item.year}}</span>
          </div>

        </div>   
    </div>

  <div class="grid right">  
      <div class="item-wrapper" v-for="(item,index) in getItems('right')"   :key="index">
        <div class="item-img" :style="{backgroundColor:item.bgcolor}">
          <img :src="require('../../common/images/'+item.name)" width="700" height="557"/>
        </div>
        <h2>{{item.title}}</h2>
        <div class="info">
          <div class="line" :style="getStyleObj(item.bgcolor)"></div>
            {{item.subtitle}}<br>
            {{item.subtitle_}}<br>
            <span>{{item.year}}</span>
          </div>
        </div>   
      </div>   
  </div> 
 </div>
</template>

<script type="text/ecmascript-6">
import {mapGetters} from 'vuex'
export default {
  data() {
    return {
    }
  },
  methods: {
    itemClick(item) {
      this.$emit('on-item-click', item)
    },
    getStyleObj(bgcolor) {
      return {
        background: `linear-gradient(to left, #CCC 50%,${bgcolor} 50%)`,
        backgroundRepeat: 'no-repeat',
        backgroundSize: '200% 3px',
        backgroundPosition: 'right 8px'
      }
    }
  },
  computed: {
    ...mapGetters(['getItems'])
  },
  components: {
  }
}
</script>

<style scoped lang="less">
  .main-wrapper{
    position: absolute;
    width: 80%;
    max-height: auto;
    left: 10%; 
    top: 200px;
    opacity: 1; 
    transition: max-height 0s 2.3s ease-out, top 1.5s cubic-bezier(0.260, 0.010, 0.000, 1.005), opacity 1.4s ease-out; 
    .grid{
      position: relative;
      width: 46%;
      .item-wrapper{ 
        position: relative;
        width: 100%;
        cursor: pointer;
        margin-bottom: 70px; 
        &:hover h2{
            margin: -28px 0 25px 30px;
        }
        &:hover .line{
              background-position: left 8px !important;
        }
        .item-img{
          position: relative;
          width: 100%;
          height: auto; 
          background-repeat: repeat-x;
          background-size: 100% 20px;
          background-position: center bottom;
          margin: 0; 
          transition: all 0.2s ease-out;
          img{
            position: relative;
            width: 100%;
            height: auto; 
            transition: all 0.2s ease-out;
            cursor: pointer; 
            margin-bottom: -5px;
          }
        }
        h2{
          position: relative; 
          font-size: 45px;
          font-weight: normal;
          color: #000;
          text-transform: uppercase;
          line-height: 48px;
          letter-spacing: 4px;
          margin: 0px 0 25px 30px;
          transition: all 0.2s ease-out;
        }
        .info{
          margin-left: 30px;
          padding-left: 0px; 
          font-size: 14px;
          letter-spacing: 2px;
          line-height: 21px;
          color: #000;
          text-transform: uppercase;
          .line{
            width: 90px;
            height: 50px;
            margin-right: 20px;
            float: left;
            transition: background-position 0.4s ease-out;
          }
          span{
            color: #aaa;
          }
        }
      } 
      .item-wrapper:last-child{
        margin-bottom: 180px;
      }
      &.left{
        float: left;
        margin-top: 0;    
        .item-wrapper:hover .item-img{ 
          margin: 0px 0 0 -10px;
          padding-top: 20px;
        } 
         .item-wrapper:hover img{
            margin: 20px 0 0 20px;
        }   
      }
      &.right{
        float: right; 
        margin-top: 120px;
        .item-wrapper:hover .item-img{
          margin: 0px 0 0 10px;  
          padding-top: 20px;
        }
        .item-wrapper:hover img{
            margin: 20px 0 0 -20px;
        } 
      }
    }  
  }
 
</style>



