<template>
  <div id='app'> 
       <fixed-header></fixed-header>
       <main-frame id="mainf" @on-item-click="itemClick"></main-frame> 
        <fixed-footer :showFooter="showFooter"></fixed-footer> 
       <fixed-line :dirClass="'line-top'"></fixed-line>
       <fixed-line :dirClass="lineBottom" :activeClass="activeClass"></fixed-line>
       <fixed-line :dirClass="'line-left'"></fixed-line>
       <fixed-line :dirClass="'line-right'"></fixed-line>
  </div>
</template>

<script>
import FixedHeader from './components/FixedHeader/FixedHeader'
import MainFrame from './components/MainFrame/MainFrame'
import FixedLine from './components/FixedLine/FixedLine'
import FixedFooter from './components/FixedFooter/FixedFooter'
export default {
  methods: {
    itemClick(item) {
      this.lineBottom += ' active'
    }
  },
  mounted() {
    window.addEventListener('scroll', (event) => {
      var scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop
      console.log('scrollHeight:' + document.documentElement.scrollHeight)
      console.log('clientHeight:' + document.documentElement.clientHeight)
      console.log('scrollTop:' + scrollTop)
      if (document.documentElement.scrollHeight - 100 < document.documentElement.clientHeight + scrollTop) {
        if (!this.showFooter) {
          this.showFooter = true
        }
      } else {
        if (this.showFooter) {
          this.showFooter = false
        }
      }
    })
  },
  components: {
    FixedHeader,
    MainFrame,
    FixedLine,
    FixedFooter
  },
  data() {
    return {
      showFooter: false,
      lineBottom: 'line-bottom',
      activeClass: 'active'
    }
  }
}
</script>

<style lang='less' >
#app {
  height: 100%;
  width: 100%;  
}
/* 开始过渡阶段,动画出去阶段 */
// .footer-enter-active{
//   transition: all 0.5s ease-out;
// }
