<template>
 <transition name="footer" > 
 <footer class="footer-wrapper" v-show="showFooter">
   <div class="legal">
     <span>happy to see u like this page</span>
     <span class="contactme">i'am glad to meet u</span>
     <br>
     © 2017 All rights reserved. Brands mentionned above are property of their respective owner.
   </div>
   <div class="link"> 
     <a >github</a>
     <a >新浪微博</a>
     <a >微信</a>
     <a >QQ</a>
   </div>
 </footer>
       </transition>
</template>

<script type="text/ecmascript-6">
export default {
  props: {
    showFooter: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {}
  },
  components: {}
}
</script>

<style scoped lang="less">
.footer-enter-active {
  transition: all .8s cubic-bezier(.16,.08,.17,1.54); 
}
.footer-leave-active {
  transition: all .3s ease;
}
.footer-enter, .footer-leave-to
/* .slide-fade-leave-active for below version 2.1.8 */ {
  transform: translateY(131px); 
}
 
.footer-wrapper {
  position: fixed;
  bottom: 0;
  left: 10%;
  width: 80%; 
  height: 110px;
  padding-top: 20px;
  border-top: 1px solid #ddd; 
  font-size: 12px;
  font-weight: normal;
  line-height: 30px;
  color: #aaa;
  -webkit-font-smoothing: antialiased;
    a{
      display: inline-block;
      float: left;
      color: #000;
      text-decoration: none;
      margin-left: 30px;
      opacity: 0.7;
      letter-spacing: 1px;
      line-height: 24px;
      -webkit-font-smoothing: antialiased;
      background: linear-gradient(to right, rgba(0, 0, 0, 1) 50%, rgba(0, 0, 0, 0.1) 50%);
      background-repeat: no-repeat;
      background-size: 200% 2px;
      background-position: right bottom; 
      transition: opacity 0.2s ease-out, background-position 0.2s ease-out, color 0.3s 2.4s ease-out;
      &:hover{
        background-position: left bottom;
        opacity: 1;
        cursor: pointer;
      }
  } 
  .legal{
    float: left;
    line-height: 24px;
    margin-top: 15px;
    max-width: 65%;
    span{
      color: #666;
      font-size: 13px; 
    }
    .contactme{
      display: inline-block;
    line-height: 24px;
    color: #000;
    -webkit-font-smoothing: antialiased;
    background: linear-gradient(to right, rgba(0, 0, 0, 1) 50%, rgba(0, 0, 0, 0.1) 50%);
    background-repeat: no-repeat;
    background-size: 200% 2px;
    background-position: right bottom;
    transition: background-position 0.2s ease-out;
    cursor: pointer;
    &:hover{
      background-position: left bottom;
    }
    }
  }
  .link{
    float: right;
    margin-top: 15px;
  }
}
</style>



